import { Component, OnInit, Input, Output } from '@angular/core';
import { markdown } from 'markdown';
import { SafeHtml, DomSanitizer } from '@angular/platform-browser';
import { Article } from '../../entity/article';
import { ArticleService } from '../../service/article.service';
@Component({
  selector: 'app-editorpage',
  templateUrl: './editorpage.component.html',
  styleUrls: ['./editorpage.component.scss'],
  providers: [ArticleService]
})
export class EditorpageComponent implements OnInit {

  @Input()
  @Output()
  article: Article;
  safeBody: SafeHtml;
  rows: number;

  constructor(private domSanitizer: DomSanitizer, private articleService: ArticleService) { }

  convert() {
    if (!this.article || !this.article.content) {
      return;
    }
    const rows = this.article.content.search('\r');
    this.rows = rows > 20 ? rows : 20;
    this.safeBody = this.domSanitizer.bypassSecurityTrustHtml(markdown.toHTML(this.article.content));
  }

  ngOnInit() {
    this.rows = 20;
    if ( !this.article) {
      this.article = new Article();
    }
    if ( !this.article.content) {
      this.article.content = '# TestTitle \r\n \r\n test content';
    }
    this.convert();
  }

  onChange() {
    this.convert();
  }

  onSubmit() {
    this.article.author = localStorage.getItem('login-user');
    const result = this.articleService.postArticle(this.article);
    result.subscribe(info => {
      console.log(info);
    });
  }

}



