import { Injectable } from '@angular/core';
import { RequestService } from './common/request.service';
import { ApiService } from './common/api.service';
import Settings from '../util/settings';
import { Observable } from 'rxjs/Observable';
import { Article } from '../entity/article';

@Injectable()
export class ArticleService {

  constructor(private requestService: RequestService) { }

  public getArticleById(articleId, parameter ?: {}): Observable<Article> {
    const url = Settings.host + Settings.apis.article + articleId;
    return this.requestService.doRequestMain<Article>(url, parameter, 'get');
  }

  public getArticleForHomePage(): Observable<Article[]> {
    const url = Settings.host + Settings.apis.article
    + encodeURI('search?qStr={"queryNumber":10,"queryConditions":[{"name":"author","option":"EQ","value":"tester"}]}');
    // const parameter = new HttpParams();
    // parameter.append('qStr', '{"queryNumber":10,"queryConditions":[ { "name":"author", "option":"EQ", "value":"tester" }]}');
    return this.requestService.doRequestMain<Article[]>(url, null, 'get');
  }

  public postArticle(article: Article): Observable<Article> {
    const url = Settings.host + Settings.apis.admin.article;
    return this.requestService.doRequestMain<Article>(url, article, 'post');
  }
}



